import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Container, Grid } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    height: "300px",
    width: "100%",
    backgroundColor: "#181818",
    bottom: 0,
    color: "#D9D9E0",
    padding: 20,
  },
}));

const Footer = () => {
  const classes = useStyles();

  return (
    <>
      <div className={classes.root}>
        <Container fixed>
          <Grid
            container
            direction="row"
            justify="space-evenly"
            alignItems="center"
          >
            <Grid item xs={12}>
              Footer
            </Grid>
          </Grid>
        </Container>
      </div>
      <Grid
        style={{ backgroundColor: "#202020", height: "75px", color: "#fff" }}
        container
        justify="center"
        alignItems="center"
      >
        © 1979-2021 ХОВД ИХ СУРГУУЛЬ, МЭДЭЭЛЛИЙН ТЕХНОЛОГИЙН ХӨГЖЛИЙН ТӨВ
      </Grid>
    </>
  );
};
export default Footer;
