import React from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";

import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

import SwipeableViews from "react-swipeable-views";
import { autoPlay } from "react-swipeable-views-utils";

import { Card, CardMedia, Dialog, Grid, Grow } from "@material-ui/core";

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const tutorialSteps = [
  {
    label: "ЦАХИМ СУРГАЛТАД ТУЛГАМДАЖ БУЙ АСУУДАЛ, ШИЙДЭЛ",
    imgPath:
      "https://images.unsplash.com/photo-1523050854058-8df90110c9f1?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80",
  },
  {
    label: "ЦАХИМ СУРГАЛТАД ТУЛГАМДАЖ БУЙ АСУУДАЛ, ШИЙДЭЛ",
    imgPath:
      "https://images.unsplash.com/photo-1492539438225-2666b2a98f93?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1953&q=80",
  },
];

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: "100%",
    flexGrow: 1,
  },

  header: {
    display: "flex",
    alignItems: "center",
    height: 50,
    paddingLeft: theme.spacing(4),
    backgroundColor: theme.palette.background.default,
  },
  media: {
    width: "100%",
    // margin: 0,
    // opacity: 0.7,
    height: "100vh",
    textAlign: "center",
  },
}));

const MainSlide = () => {
  const classes = useStyles();
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = tutorialSteps.length;
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step) => {
    setActiveStep(step);
  };
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div className={classes.root}>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <img
          style={{ width: "100%" }}
          src="http://clouduni.mn:5000/images/khu_link02.jpg"
          alt=""
        />
      </Dialog>
      <AutoPlaySwipeableViews
        interval={5000}
        axis={theme.direction === "rtl" ? "x-reverse" : "x"}
        index={activeStep}
        onChangeIndex={handleStepChange}
        enableMouseEvents
      >
        {tutorialSteps.map((step, index) => (
          <>
            <Card key={index}>
              <CardMedia
                className={classes.media}
                image={step.imgPath}
                title="Contemplative Reptile"
              >
                <Grid
                  style={{
                    height: "100%",
                    // background:
                    //   "linear-gradient(180deg, rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 1) 100%)",
                    backgroundColor: "rgba(0, 0, 0, 0.5)",
                    color: "#fff",
                  }}
                  container
                  direction="row"
                  alignItems="center"
                >
                  <Grid item xs={12}>
                    <Grow in {...(true ? { timeout: 500 } : {})}>
                      <Typography
                        // style={{ backgroundColor: "#3CA162" }}
                        gutterBottom
                        variant="h5"
                      >
                        ХОВД ИХ СУРГУУЛЬ
                      </Typography>
                    </Grow>
                    <Grow in {...(true ? { timeout: 1000 } : {})}>
                      <Typography gutterBottom variant="h2">
                        {step.label}
                      </Typography>
                    </Grow>

                    <Grow in {...(true ? { timeout: 1500 } : {})}>
                      <Button
                        onClick={handleClickOpen}
                        size="medium"
                        variant="contained"
                        style={{
                          backgroundColor: "#f1c40f",
                          margin: 10,
                          height: "50px",
                          border: "5px",
                          borderLeftColor: "#3CA162",
                          borderRadius: 0,
                          fontWeight: "bold",
                          fontSize: 14,
                        }}
                      >
                        Дэлгэрэнгүй
                      </Button>
                    </Grow>
                  </Grid>
                </Grid>
              </CardMedia>
            </Card>
          </>
        ))}
      </AutoPlaySwipeableViews>
      {/* <MobileStepper
        steps={maxSteps}
        position="static"
        variant="dots"
        activeStep={activeStep}
        nextButton={
          <Button
            size="small"
            onClick={handleNext}
            disabled={activeStep === maxSteps - 1}
          >
            {theme.direction === "rtl" ? (
              <KeyboardArrowLeft />
            ) : (
              <KeyboardArrowRight />
            )}
          </Button>
        }
        backButton={
          <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === "rtl" ? (
              <KeyboardArrowRight />
            ) : (
              <KeyboardArrowLeft />
            )}
          </Button>
        }
      /> */}
    </div>
  );
};

export default MainSlide;
